@import url('reset.css');

@import url('https://fonts.googleapis.com/css2?family=Aldrich&family=Bebas+Neue&display=swap');

:root {
  --family: 'Aldrich', sans-serif;
  --color-bg: #2C2C32;
  --color-active: #0f7dc6;
  --bg_gradient: linear-gradient(to left top, var(--link_hover_color_lblue), var(--lgray));
  --bg_gradient_invert: linear-gradient(to right top, var(--dBlue), var(--blue));
  --bg_gradient_card: linear-gradient(to left top, var(--blue), #87AECA);
  --Loader--square-gradientAngle: 45deg;
  --bg: #0E1119;
  --blue: #478CBF;
  --dBlue: #23314D;
  --orange: #F27841;
  --gray: #36414C;
  --lgray: #616B80;
  --link_hover_color_lblue: #28C2E0;
  --radius: 10px;
  --button_radius: 7px;
  --Loader-size: 20vmin;
  --Loader-borderColor: #ccc;
  --Loader-color2: #8c7cff;
  --Loader-color1: #4020ff;
  --angle: 45deg;
}

body[data-theme='dark'] {
  transition: 0, 75s ease-out;
  --colors-text: hsl(0, 0%, 100%);
  --grLoader1: var(--link_hover_color_lblue);
  --bg: #0E1119;
  --colors-ui-base: #23314D;
  --bg-header: var(--gray);
  --shadow: #28C2E0 0 0 4px;
  --shadowx2: #28C2E0 0 0 8px;
  --active-shadow: 0px 2px 5px var(--link_hover_color_lblue);
}

body[data-theme='light'] {
  transition: 0, 75s ease-out;
  --colors-text: hsl(200, 15%, 8%);
  --grLoader1: var(--gray);
  --bg: hsl(0, 0%, 100%);
  --colors-ui-base: #fff;
  --bg-header: var(#fff);
  --shadow: #616B80 0 0 4px;
  --shadowx2: #616B80 0 0 8px;
  --active-shadow: 0px 2px 5px var(--link_hover_color_lblue);
}

body {
  margin: 0;
  font-family: var(--family);
  color: var(--colors-text);
  font-weight: var(--fw-light);
  background-color: var(--bg);
  transition: 0, 75s ease-out;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.layout {
  height: 100vh;
}

a {
  color: var(--colors-text);
  text-decoration: none;
}

.header,
.footer {
  box-shadow: var(--shadow);
  background: var(--bg-header);
  margin-bottom: 1rem;
  padding: 1rem 0;
  text-align: center;
}

.header-wrapper,
.footer-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;

}

header>a {
  color: white;
  text-decoration: none;
  padding: 0 1rem;
}

header>a:hover {
  text-decoration: underline;
}

header .menu-section {
  max-width: 900px;
}

.menu-wrapper {
  max-width: 900px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-section {
  display: flex;
  width: 50%;
  justify-content: space-evenly;
  font-weight: var(--fw-bold);
  text-decoration: none;
}

.menu-section {
  display: flex;
  width: 50%;
  justify-content: space-evenly;
  align-items: center;
  font-weight: var(--fw-bold);
  text-decoration: none;
}

h1,
h2,
h3 {
  text-align: center;
  margin-top: 0;
}

.container {
  max-width: 900px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
}

.main {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.active {
  color: var(--link_hover_color_lblue);
  border-bottom: var(--active-shadow) 2px solid;
  border-radius: 5px;
  padding: 3px;
  cursor: default;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

label {
  display: flex;
  gap: 1rem;
}

.slider {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap-info-image {
  box-shadow: var(--shadow);
  border-radius: var(--radius);
  width: 80%;
  height: 320px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.slider-img {
  opacity: 1;
}

.info-image {
  border-radius: var(--radius);
  width: 100%;
  height: 320px;
  animation: fadeIn 1s ease-in-out;
  object-fit: cover;
  object-position: center;
}

.Loader {
  display: block;
  width: 100%;
  height: 150px;
  border-top-left-radius: var(--radius);
  border-top-right-radius: var(--radius);
  border: 1px solid var(--Loader-borderColor);
  animation: animateLoader 1s linear infinite;
}

.Loader--big {
  border-radius: var(--radius);
  height: 320px;
  width: 80%;
}

.Loader--square {
  background: linear-gradient(var(--angle), var(--grLoader1), var(--blue), var(--grLoader1), var(--blue));
}

.Loader--square {
  background-size: 300% 300%;
}

@keyframes animateLoader {
  0% {
    background-position: top 0% left 100%;
  }

  100% {
    background-position: top 99% left 0%;
  }
}

.models-page,
.home-page,
.how-use-page {
  min-height: calc(100vh - (25px + 25px));
  min-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

/* .home-page {} */

.up-section-home {
  width: 100%;
  height: 100vh;
  background: radial-gradient(var(--lgray) 20px, var(--bg) 50%);
}

.middle-section-home {
  width: 100%;
  height: 100vh;
}

.down-section-home {
  width: 100%;
  height: 100vh;
  background: radial-gradient(var(--lgray) 20px, var(--bg) 50%);
}

.list-wrapper {
  margin: 0 auto;
  min-width: 100%;
}

.list-models {
  margin: 0 auto;
  padding: 1rem 0;
  display: flex;
  flex-wrap: wrap;
}

.card-body {
  margin: 4px;
  border-radius: var(--radius);
  border-bottom: var(--lgray) -2px solid;
  background-color: var(--colors-ui-base);
  box-shadow: var(--shadow);
  cursor: pointer;
  overflow: hidden;
  width: 32%;
}

/* interesting effect */
/* .card-body :hover{
box-shadow: #4020ff 0 0 5px;
} */

.card-body:hover {
  box-shadow: 0px 2px 5px var(--link_hover_color_lblue);
}

.card-body:hover .model-label {
  color: var(--link_hover_color_lblue);
  border-radius: var(--radius);
  padding: -15px;
  border-radius: 5px;
  transition: 0, 75s ease-out;

}

.card-list {
  padding: 1rem;
  list-style: none;
  margin: 0;
}

.model-label {
  padding: 3px;
  box-shadow: var(--shadowx2);
  background-color: var(--lgray);
  border-radius: 5px;
  transition: 0, 75s ease-out;
}

.card-list-item {
  font-size: var(--fs-sm);
  line-height: 1.5;
  font-weight: var(--fw-light);

  &>b {
    font-weight: var(--fw-bold);
  }
}

.card-img {
  display: block;
  width: 100%;
  height: 150px;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: var(--radius);
  border-top-right-radius: var(--radius);
  box-shadow: var(--shadow);
}

button {
  min-width: 20%;
  padding: 0.2rem 2rem;
  font-family: var(--family);
  margin: 1rem;
  background-color: var(--colors-ui-base);
  box-shadow: var(--shadow);
  border-radius: var(--radius);
  border: none;
  align-self: flex-start;
  text-align: center;
  color: var(--color-text);
  cursor: pointer;
  z-index: 2;
}

.texture-img {
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}

.select-wrapper {
  color: inherit;
  background-color: inherit;
}

.ul-pages {
  display: flex;
  justify-content: center;
  margin: 2rem;

}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.list-page {
  color: var(--colors-text);
  padding: 0.5rem 0.5rem;
  font-size: 1rem;
  box-shadow: var(--shadow);

}

.active-page {
  padding: 1rem;
  background: var(--colors-ui-base);
  box-shadow: var(--active-shadow);
}



.spin-loader {
  width: 48px;
  height: 48px;
  background: #FFF;
  border-radius: 50%;
  margin: 0 auto;
  animation: rotation 1s linear infinite;
}

.spin-loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 6px;
  top: 10px;
  width: 12px;
  height: 12px;
  color: var(--grLoader1);
  background: var(--blue);
  border-radius: 50%;
  box-shadow: 25px 2px, 10px 22px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.iframe {
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}

.demo-preview {
  display: block;
  width: 20%;
  height: 570px;
  margin: 0 auto;
  background-color: var(--link_hover_color_lblue);
}

.demo-section {
  margin: 1rem auto;
  width: 320px;
  height: 160px;
}

.little-image-wrapper {
  margin: 1rem auto;
  width: 320px;
  height: 160px;
}

/* for compare component */

.compnent-wrapper_st {
  width: 622px;
  margin-left: 25%;

}

.select-section_st {
  display: flex;
  width: 622px;
  margin: 2px;
  background-color: #0E1119;
  border-radius: 4px;
}

.button_st {
  padding: 4px;
  transition: 0.75s;
  border-radius: 4px;
  font-family: 'Aldrich', sans-serif;
  box-shadow: 4px 4px 8px #fff;
  color: #fff;
  background-color: #23314D;
}

.button_st:hover {
  color: #28C2E0;
  box-shadow: 6px 6px 8px #fff;
  background-color: #616B80;
}

.button_st:active {
  background-color: #fff;
  color: #28C2E0;
  box-shadow: 8px 8px 12px #28C2E0;
}

.img-comp-container_st {
  position: relative;
  height: 240px;
  /*should be the same height as the images*/
}

.img-comp-img_st {
  border-radius: 8px;
  position: absolute;
  width: 360px;
  height: 240px;
  overflow: hidden;
}

.img-comp-img_st img {
  display: block;
  transition: 0.75s;

}

.img-comp-slider_st {
  visibility: hidden;
  position: absolute;
  z-index: 9;
  cursor: ew-resize;
  width: 50px;
  height: 30px;
  background-color: #28C2E0;
  opacity: 0.75;
  border-radius: 25%;
  animation: animateSlider 1s linear infinite;

}

.img-comp-slider_st::before {
  content: '<';
}

.img-comp-slider_st::after {
  margin-left: 18px;
  content: '>';
}

@keyframes animateSlider {
  0% {
    opacity: 0.35;
  }

  50% {
    opacity: 0.85;
  }

  100% {
    opacity: 0.35;
  }
}

.active_st {
  background-color: #fff;
  color: #28C2E0;
  box-shadow: 8px 8px 12px #28C2E0;
}
.second-img_st{
  visibility: hidden;
}
.img-comp-overlay{
  visibility: hidden;
}

@media (max-width: 767px) {
  .card-body {
    margin: 5px;
    border-radius: var(--radius);
    background-color: var(--colors-ui-base);
    box-shadow: var(--shadow);
    cursor: pointer;
    overflow: hidden;
    width: 48%;
  }
  .select-section_st {
    display: flex;
    width: 360px;
    font-size: 12px;
    /* flex-wrap: wrap; */
    justify-content: center;
    margin: 2px;
    background-color: #0E1119;
    border-radius: 4px;
  }
  .button_st {
    padding: 12px 2px;
    margin: 8px;
    transition: 0.75s;
    border-radius: 4px;
    font-family: 'Aldrich', sans-serif;
    box-shadow: 4px 4px 8px #fff;
    color: #fff;
    background-color: #23314D;
  }
  
  .button_st:hover {
    color: #28C2E0;
    box-shadow: 6px 6px 8px #fff;
    background-color: #616B80;
  }
  
  .button_st:active {
    background-color: #fff;
    color: #28C2E0;
    box-shadow: 8px 8px 12px #28C2E0;
  }
  .active_st {
    background-color: #fff;
    color: #28C2E0;
    box-shadow: 8px 8px 12px #28C2E0;
  }
}

@media (max-width: 575px) {
  .card-body {
    margin: 5px;
    border-radius: var(--radius);
    background-color: var(--colors-ui-base);
    box-shadow: var(--shadow);
    cursor: pointer;
    overflow: hidden;
    width: 98%;
  }



}